.maindiv {
    
    margin: 0 auto;
    background-color: #FDE8F4;
    width: 100%;
    position: absolute;
    height: 100vh;
    text-align: center;
    font-size: 1em;
    font-family: Arial, Helvetica, sans-serif;
    color: #020259;
}
.maindiv h1, h2, h3 {
    text-decoration: underline;
}
.maindiv img{
    height: 100px ;
    border-radius: 50%;
   position: absolute;
   left: 25%;
   top: 3%;
}
.maindiv p {
    text-align: justify;
    padding-left: 20px;
    padding-right: 20px;
    font-size: 15px;
    
}
@media only screen and (max-width: 800px) {
    .maindiv {
        height: 200%;
    }
    .maindiv img {
        left: 4%;
        top: 2%;
     }
     .maindiv p {
         font-size: 13px;
     }
     }
