header h1 {
    margin: 0;
}
header {
    margin: 0 auto;
    color:white;
    text-align: center;
    padding: 120px 20px 20px 20px; 
    font-size: 1.5em;
    
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
header a{ 
    box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 1);
    font-weight: 300;
    background-color:  #E3B120!important;
    border-color: #E3B120!important;
    border-radius: 5%;
    text-decoration: none;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;
    color: darkblue;
    display: inline-block;
}