.Skills {
    background-color: #186CDE;
    position: absolute;
    width: 100%;
    top: 285%;
    margin: 0 auto;
    height: 50vh;
    text-align: center;
    padding-bottom: 120px;
    padding-top: 120px;
}
.Skills h2{
    font-size: 48px;
    text-transform: uppercase;
    text-decoration-line: none;
    color: #fff;
    margin-bottom: 48px;
    margin-top: 0;
    padding: 0;
   
}
.Skills h3 {
    color:  #E3B120;
    text-decoration-line: none;
    text-transform: uppercase;
}
.Skills > .row {
    display: flex;
    justify-content: space-around;
}
.Skills > .row span, svg {
    background-color: #fff;
    width: 70px !important;
    height: 70px !important;
    display: inline-block;
    border-radius: 20%;
    color: #0A69D9;
    padding: 5px 5px 5px 5px;
}

.Skills > .row h4 {
    margin-bottom: 8px;
    font-family: inherit;
    font-weight: 500;
    font-size: 2em;
    color: #fff;
    text-decoration-line: underline;
}


@media only screen and (max-width: 723px) {
    .worksamples {
        top: 230%;
        margin: 0 auto;
     }
     }
